<template>
  <div class="card mx-xl-5">
    <div class="card-body">
      <form>
        <p class="h4 text-center py-4">Subscribe</p>
        <label for="defaultFormCardNameEx" class="grey-text font-weight-light"
          >Your name</label
        >
        <v-text-field dense outlined></v-text-field>
        <label for="defaultFormCardEmailEx" class="grey-text font-weight-light"
          >Your email</label
        >
        <v-text-field
          label="Your product or service"
          value="Grocery delivery"
          hint="For example, flowers or used cars"
        ></v-text-field>
        <div class="text-center py-4 mt-3">
          <v-btn class="btn btn-outline-purple" type="submit">
            Send
          </v-btn>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return { email: "", password: "", incorrectAuth: false };
  },
  methods: {
    login() {
      this.$store
        .dispatch("userLogin", { email: this.email, password: this.password })
        .then(() => {
          this.$router.push({ name: "Home" });
        })
        .catch((err) => {
          console.log(err);
          this.incorrectAuth = true;
        });
    },
  },
};
</script>
<style></style>
